.menu {
  display: flex;
  height: 1.5em;
  padding: 0 1em;
  justify-content: space-between;
  align-items: center;
  list-style-type: none; }
  .menu.menu__disabled {
    display: none; }

.menu__link {
  width: 100%;
  height: 100%;
  font-size: 0.75em;
  text-transform: uppercase;
  text-align: center; }
  .menu__link.menu__link--active {
    border-bottom: 0.125em solid black; }

.menu__link--text {
  color: black;
  text-decoration: none; }
