.sidebar {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 10em;
  height: 100%;
  margin: 0;
  list-style-type: none;
  z-index: 105;
  background: white; }
  .sidebar.sidebar--open {
    display: flex;
    flex-direction: column; }

.sidebar__link {
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 0.75em;
  text-transform: uppercase;
  text-align: center; }
  .sidebar__link.sidebar__link--active {
    background: lightblue; }

.sidebar__link--text {
  color: black;
  text-decoration: none; }
