.header {
  width: 100%;
  height: 3em;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1em; }

.header__burger {
  left: 1em;
  padding: 0.5em;
  border-radius: 50%;
  position: absolute;
  background-color: white; }
  .header__burger.header__burger--sidebar-open {
    left: 11em; }

.header__logo {
  width: 6em; }
  .header__logo.header__logo--disabled {
    display: none; }
